import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../../../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { divIcon } from 'leaflet'

const CreateGroup = () => {
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [groupType, setGroupType] = useState({ value: '', label: 'select a group type' })
    const [userInput, setUserInput] = useState({
        name: '',
        groupType: 1,
        isDefault: false,
        campaignId: 0
    })
    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
  
    const onSubmit = (e) => {
        e.preventDefault()
        localStorage.setItem('useBMStoken', true)
        const { name, isDefault } = userInput
        let { groupType } = userInput
        groupType = groupType.value
        setPointRuleloading(true)
        useJwt.createGroup({ name, isDefault, groupType }).then((response) => {
            setPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            localStorage.setItem('useBMStoken', false)
            Error(error)
            console.log(error)
          })
    }
    return (
        <Fragment>
            <Button.Ripple className='mb-1' color='primary' tag={Link} to='/allGroups' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Create Group</CardTitle>
                    
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="name">Group Name</Label>
                            <Input type="text"
                                name="name"
                                id='name'
                                value={userInput.name}
                                onChange={handleChange}
                                required
                                placeholder="group name"
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="6" >
                        <FormGroup>
                            <Label for="groupType">Group Type</Label>
                            <Select
                                theme={selectThemeColors}
                                maxMenuHeight={200}
                                className='react-select'
                                classNamePrefix='select'
                                value={{ value: groupType.value, label: groupType.label }}
                                onChange={(selected) => {
                                    setGroupType({ value: selected.value, label: selected.label })
                                }}
                                options={[{ value: 1, label: 'Customer' }, { value: 2, label: 'Agent' }, { value: 3, label: 'Merchant' }]}
                            />
                        </FormGroup>
                    </Col>
                    <Col sm="12" className='mb-1'>
                            <FormGroup check>
                                <Input onChange={(e) => {
                                        if (e.target.checked) { 
                                            setUserInput({ ...userInput, isDefault: true })
                                        } else {
                                            setUserInput({ ...userInput, isDefault: false })
                                        }
                                    }
                                } type='checkbox' id='isDefault' />
                                <Label for='isDefault' check>
                                    Is Default?
                                </Label>
                            </FormGroup>
                        </Col> 

                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreateGroup